import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { Provider } from "react-redux";
import store from "./Redux/ReduxStore";
import { BrowserRouter as Router, Route } from 'react-router-dom'

const GetCollection = ({ match }) => {
  return <App collectionId={match.params.collectionId}></App> 
}

ReactDOM.render(
  <Provider store={store}> 
    <Router>
      <div>
        <Route exact path="/TrendyolCollectionShare" component={App} />
        <Route exact path="/TrendyolCollectionShare/:collectionId" component={GetCollection} />
      </div>
    </Router>        
  </Provider>
  , document.getElementById('root')
);


serviceWorker.unregister();
