import React, { Component } from 'react';
import { connect } from 'react-redux';
import { ThemeProvider } from "@material-ui/styles";
import {createMuiTheme} from "@material-ui/core";
import Main from './Components/Main';
import CssBaseline from '@material-ui/core/CssBaseline';
import Switch from '@material-ui/core/Switch';

export class App extends Component {
  constructor(){
    super();

    this.state={
      switchCheck:false,
      theme : {
        palette: {
          type: "light"
        }
      }
    }
  }

  toggleDarkTheme = () => {
    let newPaletteType = this.state.theme.palette.type === "light" ? "dark" : "light";
    this.setState({
      theme:{
        palette: {
          type: newPaletteType
        }
      }
    });
  };

  handleSwitchChange = (event) => {
    this.toggleDarkTheme()
    this.setState({switchCheck:event.target.checked});
  };

  componentDidMount(){
    console.log(this.props.collectionId);
  }
  
  render() {
    const muiTheme = createMuiTheme(this.state.theme);

    return (
      <ThemeProvider theme={muiTheme}>
        <CssBaseline/>
        <Switch
          checked={this.state.switchCheck}
          onChange={this.handleSwitchChange}
        />
        <Main/> 
      </ThemeProvider> 
    )
  }
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = (dispatch)=> {

}

export default connect(mapStateToProps, mapDispatchToProps)(App)
