import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Fab from '@material-ui/core/Fab';
import LinkIcon from '@material-ui/icons/Link';
import Typography from '@material-ui/core/Typography';
import AddNewList from './AddNewList';
import { saveCollection } from "../Redux/ReduxActions";

const useStyles = {
    root:{
        flexGrow: "1",
        maxHeight:"250px",
        minHeight:"250px"
    },
    date :{
        position :"absolute",
        top:"210px",
        color : "lightcyan",
        fontWeight: "bold"
    },
    Image:{
        maxWidth:"100%",
        width:"100%",
        objectFit: "cover",
        maxHeight:"250px"
    },
    fab: {
        position: "fixed",
        top: "220px",
        left:"75%",
        zIndex :"3"
    },
    imageIcon :{
        height : "50px",
        width : "50px",
        position: "absolute",
        left: "50%",
        top: "50%",
        transform: "translate(-50%, -50%)"
    },
    trendyolLogo:{
        height : "200px",
        width : "100%",
        position: "absolute",
    },
    collection :{
        position :"absolute",
        left:"50%",
        top:"210px",
        fontFamily: "source_sans_proregular"
    },
};

export class Header extends Component {
    constructor(){
        super();
        this.state={
            NewListOpen : false,
        }
    }
    
    handleClose=()=>{
        this.setState({NewListOpen:false});
    }

    handleOpen=()=>{
        //this.setState({NewListOpen:true});
        this.props.saveCollection();
    }

    render() {
        const {classes} = this.props;

        return (
            <div className={classes.root}>
                <img className={classes.trendyolLogo}src="https://cdn.dsmcdn.com/web/logo/ty-logo.svg" alt="https://cdn.dsmcdn.com/web/logo/ty-logo.svg"/>      
                <Typography className={classes.collection} variant="h2" component="h1">
                    KOLEKSİYONUN
                </Typography>             
                <Fab className={classes.fab} color="primary" aria-label="add">
                    <LinkIcon  onClick={this.handleOpen} />
                </Fab> 
                <AddNewList open={this.state.NewListOpen} handleClose={this.handleClose}></AddNewList> 
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = (dispatch) => {
    return {
        saveCollection: Todos => dispatch(saveCollection(Todos))
    }
}

const HeaderComponent = withStyles(useStyles)(Header);
export default connect(mapStateToProps, mapDispatchToProps)(HeaderComponent)
