import React, { Component } from 'react';
import { withStyles,createMuiTheme } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import Container from '@material-ui/core/Container';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import SearchIcon from '@material-ui/icons/Search';
import Detail from './Detail';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { selectedTodo } from "../Redux/ReduxActions";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import AccordionActions from '@material-ui/core/AccordionActions';
import Divider from '@material-ui/core/Divider';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import Paper from '@material-ui/core/Paper';
import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';

import ItemImagesModel from '../Models/ItemImagesModel';
import CollectionItemModel from '../Models/CollectionItemModel';
import { updateCollection, saveCollection } from "../Redux/ReduxActions";

import TrendyolModel from '../TrenyolModels/TrendyolModel';

const theme = createMuiTheme();
const useStyles = {
    root:{
        flexGrow: "1",
        marginTop :"30px"
    },
    list: {
      width: '100%',
    },
    li_item_tag:{
        height: "20px",
        padding: "4px 8px",
        fontSize: "10px",
        minWidth: "20px",
        alignItems: "center",
        lineHeight: "10px",
        borderRadius: "2px",
        marginLeft : "10px"
    },
    gridList: {
        flexWrap: 'nowrap',
        transform: 'translateZ(0)',
    },
    title: {
        color: theme.palette.primary.light,
    },
    titleBar: {
        background:'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    },    
    paper:{
        flexGrow : "1",
        height : "80px",
        weight : "50px"
    },
    slide:{
        display: "flex",  
        flexDirection :"column"      
    },
    ImageStyle:{
        height :"100%",
        weight:"100%"
    },
    column: {
        flexBasis: '33.33%',
    },
  };

export class CollectionList extends Component {
    constructor(){
        super();

        this.state={
            Link : "",
            DetailOpen : false
        }
    }

    
    handleDetailClose=()=>{
        this.setState({DetailOpen:false});
    }

    handleChangeLinkText=(e)=>{
        this.setState({Link:e.target.value});
    }
    handleAddCollectionItem=(e)=>{
        const herokuHttps = "https://cors-anywhere.herokuapp.com/";
        
        // const request = require('request');
        // request(herokuHttps+this.state.Link, function (error, response, body) {
        //   console.error('error:', error); // Print the error if one occurred
        //   console.log('statusCode:', response && response.statusCode); // Print the response status code if a response was received
        //   console.log('body:', body); // Print the HTML for the Google homepage.
        // });

        
        // var quizUrl = herokuHttps+this.state.Link;
        // fetch(quizUrl).then(function(response) {
        //     return response.text()
        // })
        // .then((text)=>{
        //     console.log(text);
        //     let response = document.createElement('html');
        //     response.innerHTML = text;

        //     let TempCollection = {...this.props.MyCollection};
        //     let NewItem = new CollectionItemModel();
        //     let ItemElement = Array.from(response.getElementsByTagName("h1"))[0];

        //     NewItem.ItemName = ItemElement.firstElementChild.innerText;
        //     NewItem.ItemDescription = ItemElement.innerText;
        //     NewItem.URL = this.state.Link;
        //     NewItem.Price = 523.23;

        //     // let Images = Array.from(response.images);
        //     // console.log(Images);
        //     // Promise.all(Images.map((data)=>{
        //     //     console.log(data);
        //     //     if(data.alt === "gallery image"){
        //     //         let NewImage = new ItemImagesModel();
        //     //         let Dom =  document.createElement( 'html' );
        //     //         Dom.innerHTML = data.outerHTML;
        //     //         NewImage.URL = Dom.getElementsByTagName("img")[0].getAttribute('lazy'); 
        //     //         NewImage.HtmlContent = data.outerHTML;
        //     //         NewItem.Images.push(NewImage);
        //     //     }
        //     // }));

        //     // TempCollection.CollectionItems.push(NewItem);
        //     // this.props.updateCollection(TempCollection);            
        //     this.setState({Link:""});            
        // })
        // .catch(function(err) {
        //     console.log(err) // this won't trigger because there is no actual error
        // });

        this.getHTML(this.state.Link,(response)=>{
            let ScriptsArray =Array.from(response.getElementsByTagName("script"));
            let JSONData = ScriptsArray.find(x=>x.innerText.indexOf("window.__PRODUCT_DETAIL_APP_INITIAL_STATE__")!==-1).innerText.trim().substring("window.__PRODUCT_DETAIL_APP_INITIAL_STATE__ =".length);
            JSONData = JSONData.substring(0,JSONData.length-1);
            let NewTrendyolModel = new TrendyolModel();
            console.log(Object.assign(NewTrendyolModel,JSON.parse(JSONData)));
            
            let TempCollection = {...this.props.MyCollection};
            let NewItem = new CollectionItemModel();

            NewItem.ItemName = NewTrendyolModel.product.productCode;
            NewItem.ItemDescription = NewTrendyolModel.product.name;
            NewItem.URL = this.state.Link;
            NewItem.Price = NewTrendyolModel.product.price.sellingPrice.value;
            NewItem.Gender = NewTrendyolModel.product.gender.name;
            NewItem.FavoriteCount = NewTrendyolModel.product.favoriteCount;
            NewItem.TotalRatingCount = NewTrendyolModel.product.ratingScore.totalRatingCount;
            NewItem.TotalCommentCount = NewTrendyolModel.product.ratingScore.totalCommentCount;
            NewItem.AverageRating = NewTrendyolModel.product.ratingScore.averageRating;
            NewItem.MerchantName = NewTrendyolModel.product.merchant.name;
            NewItem.MerchantOfficialName = NewTrendyolModel.product.merchant.officialName;

            NewTrendyolModel.product.images.map((data)=>{
                let NewImage = new ItemImagesModel();
                NewImage.URL = "https://cdn.dsmcdn.com/"+data; 
                NewImage.HtmlContent = "";
                NewItem.Images.push(NewImage);
            })

            TempCollection.CollectionItems.push(NewItem);
            this.props.updateCollection(TempCollection);            
            this.setState({Link:""});
        });
    }

    handleDeleteCollectionItem=(e,p)=>{
        e.preventDefault();

        let TempCollection = {...this.props.MyCollection};
        let EntryIndex = TempCollection.CollectionItems.map((e)=> { return e.Id }).indexOf(p);
        if (EntryIndex !== -1) {
            TempCollection.CollectionItems.splice(EntryIndex, 1);
            this.props.updateCollection(TempCollection);
        }
    }

    handleOpenLink=(e,p)=>{
        e.preventDefault();
        window.open(p);
    }

    handleOpenTodo=(e,p)=>{
        e.preventDefault();
        // let FilteredTodoList = this.props.TodoLists.Todolists.find((value)=>{return value.Id.match(p)});
        // let TempTodos =Object.assign(new TodoListModel(),FilteredTodoList);
        // if(TempTodos != null || TempTodos.length > 0){
        //     this.props.selectedTodo(TempTodos);
        // }
        // this.setState({DetailOpen:true});
    }

    getHTML = ( url, callback ) => {

        const herokuHttps = "https://cors-anywhere.herokuapp.com/";
        // Feature detection
        if ( !window.XMLHttpRequest ) return;
    
        // Create new request
        var xhr = new XMLHttpRequest();
    
        // Setup callback
        xhr.onload = function() {
            if ( callback && typeof( callback ) === 'function' ) {
                callback( this.responseXML );
            }
        }
    
        // Get the HTML
        xhr.open( 'GET',herokuHttps+ url );
        xhr.responseType = 'document';
        xhr.send();
    
    };    

    render() {
        const {classes} = this.props;

        return (
            <div className={classes.root}>
                <Container maxWidth="md">
                    <TextField
                        id="Assinged-Hotkey-2"
                        placeholder="Eklenmesini istediğiniz ürünün linki ... "
                        fullWidth                        
                        InputProps={{
                            endAdornment: 
                            <InputAdornment position="end">
                                <IconButton onClick={this.handleAddCollectionItem}>
                                    <AddIcon/>
                                </IconButton>                                
                            </InputAdornment>,
                          }}
                        onChange={(e)=>this.handleChangeLinkText(e)}
                        value={this.state.Link}
                    />   
                    <List className={classes.list}>
                        {
                            this.props.MyCollection.CollectionItems != null ? 
                            this.props.MyCollection.CollectionItems.map(Item=>{
                                return (
                                    <Accordion>
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-label="Expand"
                                            aria-controls="additional-actions1-content"
                                            id="additional-actions1-header"
                                            onClick={this.handleOpenMail}
                                        >
                                            <Typography>{Item.ItemName}</Typography>
                                            <Typography color="secondary">
                                                {" — "+Item.ItemDescription}
                                            </Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            <div className={classes.slide}>
                                                <div>
                                                    <div className={classes.column}>
                                                        <Typography color="textSecondary">
                                                            {Item.FavoriteCount} Kişinin favorisi
                                                        </Typography>  
                                                        <Typography color="textSecondary">
                                                            Satıcı Adı: {Item.MerchantName}
                                                        </Typography>    
                                                        <Typography color="textSecondary">
                                                            Resmi Adı: {Item.MerchantOfficialName}
                                                        </Typography>                                                                                                         
                                                    </div>
                                                    <div className={classes.column}>
                                                        <Typography color="textSecondary">
                                                            Fiyatı : {Item.Price}
                                                        </Typography>  
                                                        <Typography color="textSecondary">
                                                            Toplam Değerlendirme : {Item.TotalRatingCount}
                                                        </Typography>      
                                                        <Typography color="textSecondary">
                                                            Toplam Yorum : {Item.TotalCommentCount}
                                                        </Typography>   
                                                        <Typography color="textSecondary">
                                                            5 Üzerinden Değerlendirme  : {Item.AverageRating}
                                                        </Typography>                                                                                                                                                                      
                                                    </div>                                                
                                                </div>                                          
                                                {                  
                                                    <GridList className={classes.gridList} cols={2.5}>
                                                        {Item.Images.map((Img)=>{
                                                            return(
                                                            <GridListTile key={Img.Id} style={{width:"50px" , height:"80px"}}>
                                                                <Zoom>
                                                                    <img
                                                                        style={{width:"100%" , height:"100%"}}
                                                                        alt={Img.Id}
                                                                        src={Img.URL}
                                                                    />
                                                                </Zoom>

                                                            </GridListTile>)
                                                        })}
                                                    </GridList>                                                                                
                                                }                                        
                                            </div>                                          
                                        </AccordionDetails>
                                        <Divider />
                                        <AccordionActions>
                                          <Button size="small" onClick={(e,p)=>this.handleOpenLink(e,Item.URL)}>Ürüne Git</Button>
                                          <Button size="small" color="secondary" onClick={(e,p)=>this.handleDeleteCollectionItem(e,Item.Id)}>
                                            Sil
                                          </Button>
                                        </AccordionActions>                                        
                                    </Accordion>
                                )
                            })
                            : ""
                        }
                    </List>
                    <Detail open={this.state.DetailOpen} handleClose={this.handleDetailClose}></Detail>
                </Container>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return { MyCollection: state.MyCollection };
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateCollection: Todos => dispatch(updateCollection(Todos)),
        saveCollection: Todos => dispatch(saveCollection(Todos))
    }
}

const CollectionListComponent = withStyles(useStyles)(CollectionList);
export default connect(mapStateToProps, mapDispatchToProps)(CollectionListComponent)
